@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
* {
   font-family: 'Rubik', sans-serif;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.leaflet-container {
   height: calc(100vh - 230px);
   width: 100%;
}

.alert {
   position: absolute;
   top: 130px;
   left: 10%;
   right: 10%;
   background-color: #e52b2b;
   color: #eee;
   padding: 10px;
   border-radius: 12px 12px 0 0;
   z-index: 3;
   opacity: 0;
}
.search {
   display: flex;
   align-items: center;
   background: white;
   height: 47px;
   width: 80%;
   padding-left: 10px;
   border-radius: 9px;
   overflow: hidden;
}

.search > input {
   background: none;
   height: 100%;
   border: 0;
   width: 100%;
   outline: none;
   font-size: 18px;
}

.search > button {
   height: 100%;
   background-color: black;
   border: 0;
   width: 50px;
}

@media only screen and (min-width: 600px) {
   .search {
      width: 40%;
   }
}

::-webkit-input-placeholder {
   font-size: 13px;
}

:-ms-input-placeholder {
   font-size: 13px;
}

::placeholder {
   font-size: 13px;
}
header {
   width: 100%;
   height: 230px;
   display: flex;
   flex-direction: column;
   align-items: center;
   color: white;
   background-repeat: no-repeat;
   background-position: center;
   background-size: cover;
}

header > div {
   font-size: 27px;
   font-weight: 500;
   margin-top: 20px;
   width: auto;
}

.Map {
   z-index: 0;
}
.CardInfo {
   display: flex;
   align-items: center;
   justify-content: center;
}

.CardInfo > div:nth-child(1) {
   display: flex;
  
   flex-direction: column;
   text-align: center;
   background: white;
   width: 80%;
   position: absolute;
   z-index: 1;
   color: hsl(0, 0%, 59%);
   text-transform: uppercase;
   font-weight: 700;
   font-size: 9px;
   border-radius: 11px;
   top: 140px;
}

.CardInfo > div:nth-child(1) > div > p:nth-child(2) {
   font-size: 16px;
   color: hsl(0, 0%, 17%);
   margin-top: 0;
}

@media only screen and (min-width: 600px) {
   .CardInfo > div:nth-child(1) {
      flex-direction: row;
   }
   .CardInfo > div:nth-child(1) > div {
      width: 100%;
      min-height: 50px;
      text-align: left;
      padding: 30px;
   }
}

